}

body, h1, h2, h3 {
  font-family: "Lato", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: 700;
}

.wrapper {
  display: center;
  padding: 7%;
  padding-top: 7%;
}

.navbar {
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  background: #343844;
  margin-bottom: 15px;
}

table {
  padding: 3%;
  border: 2px solid #e7e7e7;
  background-color: red;
}

thead {
  color: #bfc0c0;
  background-color: #343844;
  -webkit-align-items: left;
          align-items: left;
}

table-hover:hover {
  background: black;
}

.calcTable, .table-responsive {
  border: none;
}

.subnet-form {
  width: 50%;
  margin-bottom: 10px;
}

.btn-primary{
  background-color: #556e96;
  border: none;
}
.btn-primary:hover {
